import {
  Component,
  OnInit,
  Injector,
  ViewChild,
  ViewChildren,
  QueryList,
  ElementRef,
} from '@angular/core';
import { FormArray, FormControl, FormGroup } from '@angular/forms';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { BaseForm } from 'src/app/class/base.form';
import { environment } from 'src/environments/environment';
import { URLz } from 'src/app/enums/url.enum';
import Swal from 'sweetalert2';
import { HttpErrorResponse } from '@angular/common/http';
import { TransactionDetailComponent } from 'src/app/shared/components/dialogs/transaction-detail/transaction-detail.component';
import { Transaction } from 'src/app/model/transaction/full.form';
import { MatDialogConfig } from '@angular/material/dialog';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './mark-ready-posting.component.html',
  styleUrls: ['./mark-ready-posting.component.css'],
})
export class MarkReadyPostingComponent extends BaseForm implements OnInit {
  @ViewChildren('checkboxes') checkboxes: QueryList<ElementRef>;
  public dataSource = new MatTableDataSource();
  @ViewChild(MatPaginator) paginator: MatPaginator;

  public filter_data = {};
  public submitedArrayList = [];

  // table paginator data

  totalRows = 0;
  pageSize = 10;
  currentPage = 0;
  pageSizeOptions: number[] = [5, 10, 25, 100];

  // table paginator data

  public selectAllList: Array<any | number> = [];
  public multiSelect = false;
  public showTable = false;

  public displayedColumns = [
    'Select',
    'actions',
    'Receipt_No',
    'Receipt_Date',
    'receipt_type',
    'donor_name',
    'donor_mobile',
  ];

  constructor(injector: Injector) {
    super(injector);
    this.param.endpoint = URLz.READY_FOR_ERP;
  }

  ngOnInit(): void {
    this.dataSource.paginator = this.paginator;
    this._component = 'Mark Ready For Posting';
    this.initForm();
  }

  initForm() {
    this._fs._form = this._fb.group({
      receipt_type: [''],
      details: this._fb.array([]),
    });
  }

  getStoreFilter(event: any) {
    this.filter_data = event;
    if (event !== '' && event !== undefined && event !== null) {
      this._http
        .get({
          url: environment.TRANSACTION,
          endpoint: URLz.READY_FOR_ERP,
          query: {
            from_date: event.selected.startDate,
            to_date: event.selected.endDate,
            bg: event.bg,
            le: event.le,
            ou: event.ou,
            su: event.su,
            receipt_type: event.receipt_type,
            limit: this.pageSize,
            page: this.currentPage + 1,
          },
        })
        .subscribe((res: any) => {
          this.paginator.pageIndex = this.currentPage;
          this.totalRows = res?.data?.totalRecords;
          this._fs._form.get('receipt_type').patchValue(4);
          this.dataSource.data = res?.data?.records;
          this.selectAllList = res?.data?.records;
          res?.data?.records.length > 0 ? (this.showTable = true) : (
            this.showTable = false,
            this._swl.noDataFound()
          );
        });
    }
  }

  detailDialog(id: string, receipt_type: any) {
    this._http
      .get({
        endpoint: URLz.TRANSACTION,
        url: environment.TRANSACTION,
        resource: id,
        query: { receipt_type: receipt_type },
      })
      .subscribe((res: any) => {
        const data: Transaction = res.data.row;
        const config: MatDialogConfig = {
          panelClass: 'dialog-responsive',
          data,
        };
        const dialogRef = this._dialog.open(TransactionDetailComponent, config);
        dialogRef.afterClosed().subscribe();
      });
  }

  uncheckAll() {
    this.checkboxes.forEach((element) => {
      element.nativeElement.checked = false;
    });
  }

  selectAll() {
    const selectedPerArr: FormArray = this._fs._form.get(
      'details'
    ) as FormArray;
    (this._fs._form.get('details') as FormArray).clear();
    this.multiSelect = !this.multiSelect;
    if (this.multiSelect) {
      if (this.selectAllList.length != 0) {
        this.selectAllList.forEach((val) => {
          if (val.checked == 1)
            selectedPerArr.push(new FormGroup({ id: new FormControl(val.id) }));
        });
      }
    } else {
      while (0 !== selectedPerArr.length) {
        selectedPerArr.removeAt(0);
      }
    }
  }

  getcheckBoxValue(val, id) {
    const selectedIdArr: FormArray = this._fs._form.get('details') as FormArray;
    if (val.target.checked) {
      selectedIdArr.push(new FormGroup({ id: new FormControl(id) }));
    } else {
      let i = 0;
      selectedIdArr.controls.forEach((ctrl: FormGroup) => {
        if (ctrl.value.id == id) {
          selectedIdArr.removeAt(i);
          return;
        }
        i++;
      });
    }
  }

  pageChanged(event: PageEvent) {
    this.pageSize = event.pageSize;
    this.currentPage = event.pageIndex;
    this.getStoreFilter(this.filter_data);
    (this._fs._form.get('details') as FormArray).clear();
    this.multiSelect = false;
  }

  checksForSubmit() {
    this.multiSelect = false;
    this.uncheckAll();
    (this._fs._form.get('details') as FormArray).clear();
  }

  _onSubmit() {
    let data;
    this._fs._form.markAllAsTouched();
    this._vs._submitted = true;
    if (this._fs._form.valid) {
      this._http.create({
        body: this._fs._form.value,
        url: environment.DEPOSITE_CASH,
        endpoint: URLz.READY_FOR_ERP})
      .subscribe({
        next: (res: any) => {
          data = res;
          if(res.data != undefined && res.data != null){
              this.checksForSubmit();
              Swal.fire({
                title: 'Processed',
                text: res.message,
                icon: 'success',
                confirmButtonColor: '#3085d6',
                confirmButtonText: '<i class="fas fa-thumbs-up"></i>',
              }).then((res) => {
                this._fs._form.reset();
                this._fhs.reloadComponent();
              });
            }
          },
          error: (httpErrorResponse: HttpErrorResponse) => {
            this._vs._error_server(httpErrorResponse.error);
          },
        });
      this._vs._submitted = false;
    } else {
      return this._fs._form.valid;
    }
  }

  // _onSubmity(ps: PartialSubmit = this._fss.defaultBehaviour) {
  //   super._onSubmity({
  //     body: (ps:PartialSubmit) => {
  //       ps.param.body = this._fs._form.value;
  //       ps.param.url = environment.DEPOSITE_CASH;
  //     },
  //     next: (ps:PartialSubmit,res) => {
  //       if(res.data != undefined && res.data != null){
  //         this.checksForSubmit();
  //         Swal.fire({
  //           title: 'Processed',
  //           text: res.message,
  //           icon: 'success',
  //           confirmButtonColor: '#3085d6',
  //           confirmButtonText: '<i class="fas fa-thumbs-up"></i>',
  //         }).then((res) => {
  //           this._fs._form.reset()
  //           this._fhs.reloadComponent();
  //         });
  //       }
  //     }
  //   })
  // }
}
