import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ACTION } from 'src/app/enums/action.enum';
import { MarkReadyPostingComponent } from './mark-ready-posting.component';
const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: '',
        redirectTo: 'mark_ready_for_posting',
        pathMatch: 'full'
      },
      {
        path: "mark_ready_for_posting",
        component: MarkReadyPostingComponent,
        data: {
          permission: [
            ACTION.EDIT,
            ACTION.ADD
          ],
          title: 'Mark Ready for Posting'
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MarkReadyPostingRoutingModule { }
